html, body{
  background-color: #1b1f27;
}
body{
  margin: 0;
  background-image: url('https://i.pinimg.com/originals/d6/c3/ac/d6c3ac09021dbc0cda8ee27837d2c795.png')
}
h1, h2, p{
  font-family: "Roboto Condensed",sans-serif;
}
h1, h2{
  color: #ecebeb;
}
h2{
  font-size: 2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
p{
  color: #ffffff;
  font-size: 1rem;
}
a { 
  color: #a0a0a0a0;
  text-decoration: none;
}
.header h1{
  display: inline-block;
  margin-bottom: 0px;
  margin-top: .5rem;
  font-size: 2.5rem;
}
.header{
  margin-bottom: 0px;
  margin-left: .3rem;
  padding-bottom: .2rem;
  background-color: rgb(44,44,44, .5);
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}
.navBar li{
  display: inline;
  background-color: rgb(156,49,45,.5);
  border-top: .2rem black solid;
  border-bottom: .2rem black solid;
  border-right: .2rem black solid;
  font-size: 1.2rem;
}
.navBar li:hover{
  background-color: rgb(156,49,45,.8);
}
.navBar li:first-child{
  border-left: .2rem black solid;
}
.navBar li a{
  color: white;
  padding-left: 2rem;;
  padding-right: 2rem;
  line-height: 1.2vmax;
}
.navBar{
  margin-top: 1rem;
  margin-bottom: 0px;
  padding-left: 0px;
}
.foreground{
  padding-top:5.5rem;
  padding-left: .5rem;
  width:100%;
  background-color: rgb(44,44,44,.5);
  padding-bottom: 1rem;
  overflow: hidden;
}
.foreground p{
  max-width: 40rem;
}
.footer{
  display: block;
  background-color: rgb(156,49,45,.8);
}
.socials{
  padding-left: 3rem;
  padding-right: 3rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  margin-right: auto;
  table-layout: fixed;
}
.socials td{
  width:15%;
}
.socials img{
  height: 2.5rem;
}